import React from 'react';
import { Title, SandwichGeneratorApp } from './commons/sandwich';
import { Stack } from './commons/commons';
import Tim from './Tim';

const SandwichGenerator: React.FC = () => {
  const [isTalking, setTalking] = React.useState(false);

  return (
    <SandwichGeneratorApp>
      <Stack>
        <Title>Hey Smelvin</Title>
        <Tim talking={isTalking} />
        <button
          onClick={() => {
            setTalking(true);
            setTimeout(() => setTalking(false), 2000);
          }}
        >
          Reccomend a sandwich place
        </button>
      </Stack>

      {isTalking && 'bleh'}
    </SandwichGeneratorApp>
  );
};

export default SandwichGenerator;
